import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators} from '@angular/forms';

import { Account, User} from './formdata-model';

@Component({
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  users: User[];
  genders = ['Male', 'Female'];
  // active = true;
  userForm: FormGroup;
  formErrors = {
    'name': '',
    'email': '',
    'message': ''
  };

  validationMessages = {
    'name': {
        'required':      'Name is required.',
        'minlength':     'Name must be at least 4 characters long.',
        'maxlength':     'Name cannot be more than 24 characters long.'
      },
    'email': {
        'required':      'email is required.',
        'pattern': 'Invalid email'
      },
    'message': {
        'required':      'message is required.'
      }


  };

  public submitted = false;

  onSubmit() {
  this.submitted = true;
  this.users = this.userForm.value;
  }

  constructor(private fb: FormBuilder) { }

  ngOnInit(): void {
    this.createForm();
  }

  createForm(): void {

      const emailRegex = '^[a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$';
      this.userForm = this.fb.group({
          'name': ['', [Validators.required,
                       Validators.minLength(4),
                       Validators.maxLength(24)]
                     ],
          'account': this.fb.group({
             'genders': this.genders[0],
             'email': ['', [Validators.required,
                            Validators.pattern(emailRegex)]],
             'message': ['', Validators.required]
          })
      });

    this.userForm.valueChanges
          .subscribe(data => this.onValueChanged(data));

    this.onValueChanged();
    }

    onValueChanged(data?: any) {

      if (!this.userForm) {return; }
      const form = this.userForm;
      // e-mail value
      //  console.log(this.userForm.get('account.email').value);

      for (const field of Object.keys(this.formErrors)) {
        this.formErrors[field] = '';
        const control = form.get(field);
        const email = form.get('account.email');
        const message = form.get('account.message');

          // console.log(email);
          // var EMAIL_REGEXP = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;


          if (control && control.dirty && !control.valid) {
            const messages = this.validationMessages[field];
            for (const key of Object.keys(control.errors) ) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }


          if (email && email.dirty && !email.valid) {
              this.formErrors['email'] = '';
                const messages = this.validationMessages['email'];
                for (const key of Object.keys(email.errors))  {
                  this.formErrors['email'] += messages[key] + ' ';
                }

                // if(email.value != (email.value.length >= 5 && EMAIL_REGEXP.test(email.value))){
                //   this.formErrors['email'] = 'invalid Email';
                // }

            }

          //
      // if(email && email.match
      // (/[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@
      // (?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/)){
          //     return null;}else {
          //
          //       return  this.formErrors.email ='invalid email';
          //
          //       }


          if (message && message.dirty && !message.valid) {
              this.formErrors['message'] = '';
                const messages = this.validationMessages['message'];
                for (const key of Object.keys(email.errors)) {
                  this.formErrors['message'] += messages[key] + ' ';
                }
            }

      }


    }



    }










