import { Component, OnInit } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';


import { Image, Content } from './gallery';
import { GalleryService } from './gallery.service';

@Component({
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})


export class GalleryComponent implements OnInit {

//  images: Observable<Image[]>;
 image: Image ;
 contents: Content;
 imageId: number;
 public currentImageIndex: number;
 public imgSrc: string;

 public condition = true;

 constructor(
    private router: Router,
    private service: GalleryService,
    private route: ActivatedRoute
 ) {}

 ngOnInit() {
  this.route.paramMap
    .switchMap((params: ParamMap) =>
      this.service.getImage(params.get('id')))
      .subscribe((image: Image) => this.image = image);

 }

 gotoImages() {
  const imageId = this.image ? this.image.id : null;
  this.router.navigate(['/home', {id: imageId}]);
}

openModal(index: any) {
  if (!index) {
    this.currentImageIndex = 1;
  }
  this.currentImageIndex = index;
  this.condition = false;
  for ( let i = 0; i < this.image.contents.length; i++) {
    if (i === this.currentImageIndex) {
      this.imgSrc = this.image.contents[i].url;
    }
  }

}


close() {
  this.condition = true;
}

prevImage() {
  this.condition = true;
  this.currentImageIndex--;
  if (this.currentImageIndex < 0) {
    this.currentImageIndex = this.image.contents.length - 1;
  }
  this.openModal(this.currentImageIndex);
}

nextImage() {
  this.condition = true;
  this.currentImageIndex++;
  if (this.currentImageIndex === this.image.contents.length) {
       this.currentImageIndex = 0;
  }
  this.openModal(this.currentImageIndex);
}





}



