import { Component, EventEmitter, Input, Output } from '@angular/core';
import { User, Account } from './formdata-model';

@Component({

  selector: 'app-submitted',
  template: `
          <div class="innerContainer clearfix">
          <div *ngIf="submitted" class="mainForm clearfix submitted">
            <h3>You submitted the following:</h3>
            <div>{{users.name}}</div>
            <div> {{users.account.genders}}</div>
             <div>{{users.account.email}}</div>
             <div>{{users.account.message}}</div>
          <button (click)="onClick()">Edit</button>
            </div>
            </div>
      `,
  styleUrls: ['./contact.component.css']

})
export class SubmittedComponent {
  @Input() users: User;
  @Input() Account: Account[];
  @Input() genders = ['Male', 'Female'];
  @Input() submitted = false;
  @Output() submittedChange = new EventEmitter<boolean>();
  onClick() { this.submittedChange.emit(false); }
}



